<template>
  <div class="tag-list">
    <ul v-if="list.length">
      <li v-for="(name, index) in list" :key="index">
        <router-link :to="{name: 'HomeTag', params: {tag: name}}">{{ name }}</router-link>
      </li>
    </ul>
  </div>
</template>

<script>
import Api from '@/api/common'
export default {
  name: 'Tags',
  data () {
    return {
      list: []
    }
  },
  created () {
    this.fetchTags()
  },
  methods: {
    fetchTags () {
      Api.getTags()
        .then(({data}) => {
          if (data.tags.length) {
            this.list = data.tags
          }
        })
    }
  }
}
</script>

